html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

a {
	text-decoration: none;
}
main {
  font-family: "Segoe UI", "Helvetica Neue", Arial, sans-serif;
  color: #3c3c3b;
  padding-top: 40px;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

.header-title {
  font-size: 36px;
  font-weight: 300;
  color: #3c3c3b;
  text-align: center;
  margin-bottom: 40px;
}
.course-list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.course-list > li {
  padding-left: 14px;
  padding-right: 14px;
  padding-bottom: 50px;
}
.cousre-list-item {
  width: 170px;
  border: solid 1px #b5b5b5;
  border-radius: 10px;
  overflow: hidden;
}

.cousre-list-item .item-cover img {
  max-width: 100%;
}

.cousre-list-item .item-info {
  padding: 10px 16px;
}

.item-info > div {
  padding-left: .5em;
  font-size: 14px;
  font-weight: normal;
}

.cousre-list-item .item-title {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  margin-bottom: 6px;
}

.cousre-list-item .item-grade {
  margin-bottom: 9px;
}

.cousre-list-item .item-genre {
  font-size: 11px;
  margin-bottom: 12px;
}

.cousre-list-item .item-link {
  font-size: 12px;
  margin-bottom: 10px;
}

.cousre-list-item .item-link > a {
  color: #00c1ff;
  font-weight: 600;
}

.cousre-list-item .item-link > a:hover {
  text-decoration: underline;
}

.cousre-list-item .item-btn {
  width: 100%;
  margin: 0 auto;
  padding: 5px;
  border-radius: 6px;
  background: #00c1ff;
  cursor: pointer;
  color: #fff;
  border: none;
}

.cousre-list-item .item-btn:hover {
  background: #009ace;
}
.search-results {
  height: 50px;
  text-align: center;
  font-size: 24px;
  font-weight: 300;
  color: #3c3c3b;
}

.search-panel {
  display: -webkit-flex;
  display: flex;
  padding-bottom: 30px;
  padding-left: 14px;
  padding-right: 14px;
}

.search-panel select {
  width: 100%;
  padding: .3em .5em;
  height: 2.3em;
  background-color: #fff;
  border: 1px solid #706f6f;
  border-radius: 6px;
}

.search-panel > div {
  width: 25%;
  padding: 0 22px 0 0;
}

.search-panel .search {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  padding-right: 0;
}

.search-panel .search input {
  box-sizing: border-box;
  display: inline-block;
  padding: 0 15px;
  font-size: 14px;
  vertical-align: middle;
  border: 1px solid #706f6f;
  border-radius: 6px;
  padding: .45em .5em;
}

.search-panel .search button {
  display: inline-block;
  margin-left: 6px;
  width: 32px;
  height: 32px;
  line-height: normal;
  background: #fff url(https://www.imumk.ru/Content/skins/bubble/images/icons/icon-search-25.png) center no-repeat;
  border: 0;
  border-radius: 4px;
  cursor: pointer;
}
.toggle-price {
  padding: 15px;
}

.toggle-price > button {
  color: #fff;
  background: #00c1ff;
  border: none;
  border-radius: 5px;
  padding: 7px 15px;
  cursor: pointer;
}

.toggle-price > .active {
  font-weight: bold;
  background-color: #005d7c;
  padding: 8px 17px;
}


